body {
    margin: auto;
    width: 500px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

button {
    margin-left: 5px;
}

div {
    margin: 10px 0 10px 4px;
}

.error {
    color: red;
    background: wheat;
    border: solid;
    font-size: 20px;
    border-radius: 10px;
    padding: 3px 5px 6px;
    margin-bottom: 20px;
}

.success {
    color: green;
    background: lightgreen;
    border: solid;
    font-size: 20px;
    border-radius: 10px;
    padding: 3px 5px 6px;
    margin-bottom: 20px;
}

.person {
    color: grey;
    margin: 10px 0;
    list-style: square;
}

h2.title {
    text-align: center;
    font-size: xx-large;
}

input.search {
    width: 30%;
    padding: 2px 10px;
    margin-left: 32%;
    font-size: x-large;
    font-family: Geneva, Tahoma, sans-serif;
    color: white;
    background-color: black;
    border-radius: 5px;
    /* text-align: center; */
}

input.search:focus {
    background-color: #7A6D70;
}

.entry {
    /* padding: 0 -5px; */
    margin: 0;
    /* border: 1px solid black; */
}

.value {
    padding: 5px 0px;
    border-radius: 4px;
}
